h1 {
  z-index: 9999;
  font-size: 70px;
  color: white;
  font-family: sans-serif; }

h1::after {
  content: '|';
  height: 20px;
  width: 20px; }

.typewriter-container {
  margin-top: -60px;
  width: 100%;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  display: -webkit-flex;
  -ms-flex-item-align: center;
      align-self: center;
  -webkit-align-self: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100vh !important; }

@media screen and (max-width: 480px) {
  h1, h2, h3, h6 {
    text-align: center; }
  h1 {
    font-size: 50px !important; } }



